@charset "UTF-8";
/*Обнуление*/
@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;0,700;0,900;1,400;1,500;1,700&display=swap");
* {
  padding: 0;
  margin: 0;
  border: 0;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  position: relative;
  overflow-x: hidden;
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 10px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
  max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

/*--------------------*/
body {
  background: #FDFDFD;
  background-position: top center;
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  line-height: 2.2rem;
  font-weight: 400;
  color: #252525;
}

.mobile__logo-link {
  display: none;
}
.mobile__logo {
  max-width: 26rem;
}
.mobile__title {
  display: none;
  text-align: center;
  margin: 0 auto 2rem;
  max-width: 59rem;
  text-transform: uppercase;
  color: #005DAB;
  font-size: 2.4rem;
  font-weight: 700;
}
.mobile__title span {
  text-transform: none;
  color: #005DAB;
  font-size: 1.6rem;
  font-weight: 400;
}

.page__inner {
  position: relative;
  display: flex;
  gap: 2rem;
}
.page__global {
  max-width: 75rem;
  width: 100%;
  margin: 0 auto;
}
.page__card {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 2rem;
}
.page__card:last-child {
  margin-bottom: 0;
}
.page__title {
  font-size: 1.8rem;
  font-weight: 500;
  color: #005DAB;
}
.page__subtitle {
  font-weight: 700;
  color: #005DAB;
}
.page__text {
  font-size: 1.4rem;
  line-height: 1.8rem;
}
.page__text span {
  display: block;
}
.page__link {
  padding: 0.5rem 0;
  font-size: 1.6rem;
  color: #252525;
  text-decoration: underline;
  transition: color 0.3s;
}
.page__link:hover {
  color: #005DAB;
}

.btn {
  color: #fff;
  background: #005DAB;
}

a {
  color: #252525;
}

.container {
  max-width: 108rem;
  padding: 0 1rem;
  margin: 0 auto;
}

.wrapper {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.wrapper ul,
.wrapper ol {
  padding-left: 6rem;
}
.wrapper ul li {
  list-style: disc;
  font-size: 1.4rem;
  line-height: 1.8rem;
  margin-bottom: 0.5rem;
}
.wrapper ol li {
  font-size: 1.4rem;
  line-height: 1.8rem;
  margin-bottom: 0.5rem;
}

iframe {
  display: block;
  margin: 0 auto 2rem;
  border-radius: 1rem;
  transition: all 0.3s ease;
}
iframe:hover {
  transform: scale(1.5);
}

.caption {
  font-size: 1.2rem;
  font-weight: 400;
  font-style: italic;
  text-align: center;
}

.description {
  font-style: italic;
}

.breadcrumb {
  padding: 0 0 1rem;
  color: rgba(37, 37, 37, 0.7);
  line-height: 2rem;
}
.breadcrumb__item {
  display: inline;
  font-size: 1.1rem;
  font-weight: 400;
}
.breadcrumb__item + .breadcrumb__item::before {
  content: "/ ";
  padding-left: 0.5rem;
}
.breadcrumb__link {
  color: rgba(37, 37, 37, 0.7);
  transition: all 0.3s;
  text-decoration: underline;
}
.breadcrumb__link:hover {
  text-decoration: underline;
  padding-bottom: 0.5rem;
  color: rgba(37, 37, 37, 0.9);
}

.header {
  padding-top: 1rem;
  margin-bottom: 3rem;
}
.header__logo {
  max-width: 26rem;
}
.header__main {
  display: flex;
  align-items: flex-end;
  gap: 3rem;
  margin-bottom: 2rem;
}
.header__top {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 1rem;
}
.header__lang {
  display: flex;
  gap: 0.5rem;
  transform: translateY(25%);
}
.header__title {
  max-width: 59rem;
  text-transform: uppercase;
  color: #005DAB;
  font-size: 2.2rem;
  font-weight: 700;
}
.header__title span {
  text-transform: none;
  color: #005DAB;
  font-size: 1.6rem;
  font-weight: 40;
}
.header__form {
  position: relative;
  border-bottom: 1px solid #005DAB;
}
.header__form-search {
  width: 14rem;
  padding-left: 0.5rem;
  padding-right: 2rem;
}
.header__form-search::placeholder {
  color: #005DAB;
}
.header__form-btn {
  position: absolute;
  background: transparent;
  right: 0;
  width: 2rem;
}
.header svg path {
  fill: transparent;
  fill: none;
}

.menu__list {
  display: flex;
  justify-content: center;
}
.menu__list-item {
  position: relative;
  width: 100%;
  border-top: 1px solid #005DAB;
  border-bottom: 1px solid #005DAB;
}
.menu__list-item:last-child ul {
  width: 100%;
}
.menu__list-item:last-child ul > li {
  width: 100%;
}
.menu__list-item:first-child ul > li {
  width: 100%;
}
.menu__list-item:hover .menu__sublist {
  display: block;
}
.menu__list-link {
  display: block;
  background: #FDFDFD;
  padding: 1.5rem 1rem;
  font-size: 1.4rem;
  line-height: 1.7rem;
  text-align: center;
  font-weight: 500;
  color: #005DAB;
  transition: all 0.3s;
}
.menu__list-link:hover {
  color: #FDFDFD;
  background: #005DAB;
}
.menu__sublist {
  z-index: 10;
  display: none;
  position: absolute;
  left: 0;
  top: 100%;
  border: 1px solid #005DAB;
}
.menu__sublist-item {
  width: 40rem;
  background: #FDFDFD;
  transition: all 0.3s;
}
.menu__sublist-item:hover {
  background: #005DAB;
}
.menu__sublist-link {
  display: block;
  padding: 0.5rem 1rem;
  color: #005DAB;
  font-size: 1.3rem;
  text-align: left;
  width: 100%;
}
.menu__sublist-link:hover {
  color: #FDFDFD;
}
.menu__btn {
  display: none;
}

.aside {
  width: 25%;
  text-align: center;
}
.aside__menu {
  border: 1px solid #005DAB;
  margin-bottom: 1rem;
}
.aside__list-title {
  background: #005DAB;
  padding: 0.5rem 0;
  font-size: 1.6rem;
  color: #FDFDFD;
  font-weight: 400;
  margin-bottom: 1rem;
}
.aside__list-title a {
  color: #FDFDFD;
}
.aside__list-item {
  position: relative;
  width: 100%;
  margin-bottom: 1rem;
}
.aside__list-item:hover .aside__sublist {
  display: block;
}
.aside__list-link {
  display: block;
  padding: 0.5rem 0;
  font-size: 1.4rem;
  color: #252525;
  transition: color 0.3s;
}
.aside__list-link:hover {
  color: #005DAB;
}
.aside__sublist {
  display: none;
}
.aside__sublist-item {
  transition: all 0.3s;
}

.main__top {
  display: flex;
  justify-content: space-around;
  margin-bottom: 2rem;
}
.main__list-title {
  font-size: 1.8rem;
  color: #005DAB;
  font-weight: 500;
  margin-bottom: 2rem;
}
.main__list-item {
  margin-bottom: 1rem;
}
.main__list-link {
  padding: 0.5rem 0;
  font-size: 1.6rem;
  color: #252525;
  transition: color 0.3s;
}
.main__list-link:hover {
  color: #005DAB;
}
.main__preview {
  display: flex;
  justify-content: space-between;
}
.main__img {
  display: block;
  width: 100%;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.3);
  border-radius: 1rem;
}
.main__img-box {
  max-width: 50rem;
  padding: 0 1.4rem;
}

.certificates {
  display: flex;
  gap: 2rem;
  margin: 0 auto;
}
.certificates__img-box {
  transition: all 0.3s ease;
}
.certificates__img-box:hover {
  transform: scale(1.3);
}
.certificates__img {
  width: 100%;
  height: 100%;
  box-shadow: 0px 4px 7px 0px rgba(0, 0, 0, 0.2);
}

.vacancy-resume__link {
  text-decoration: underline;
  transition: all 0.3s ease;
}
.vacancy-resume__link:hover {
  color: #005DAB;
  text-decoration: underline;
}
.vacancy-resume__form {
  display: flex;
  flex-direction: column;
}
.vacancy-resume__form-warning {
  font-size: 1.2rem;
}
.vacancy-resume__form-item {
  padding: 1rem 1.5rem 1rem;
  margin-bottom: 1rem;
  font-size: 1.4rem;
  border: 1px solid #FDFDFD;
  border-bottom: 1px solid #005DAB;
  text-align: center;
  transition: border 0.5s;
}
.vacancy-resume__form-item::placeholder {
  font-size: 1.4rem;
  text-align: center;
}
.vacancy-resume__form-item:focus {
  border: 1px solid #005DAB;
  color: #252525;
}
.vacancy-resume__form-item:focus::placeholder {
  color: #FDFDFD;
}
.vacancy-resume__form-info {
  display: flex;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
.vacancy-resume__form-info input {
  display: block;
  width: 100%;
  border: 1px solid #FDFDFD;
  border-bottom: 1px solid #005DAB;
  padding: 0.8rem;
  font-size: 1.4rem;
  margin-bottom: 0.5rem;
  transition: border 0.5s;
}
.vacancy-resume__form-info input:focus {
  border: 1px solid #005DAB;
  color: #252525;
}
.vacancy-resume__form-block {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  padding: 0.5rem;
}
.vacancy-resume__form-block p {
  font-size: 1.4rem;
  line-height: 1;
  margin-bottom: auto;
}
.vacancy-resume__form-more {
  text-align: center;
  margin: 0 auto;
  width: 100%;
}
.vacancy-resume__form-more p {
  margin-bottom: 1rem;
}
.vacancy-resume__form-more textarea {
  border: 1px solid #FDFDFD;
  border-bottom: 1px solid #005DAB;
  border-top: 1px solid #005DAB;
  width: 50%;
  height: 5rem;
  padding: 0.5rem;
  resize: none;
  transition: border 0.5s;
}
.vacancy-resume__form-more textarea::placeholder {
  text-align: center;
}
.vacancy-resume__form-more textarea:focus {
  border: 1px solid #005DAB;
  color: #252525;
}
.vacancy-resume__form-btn {
  display: block;
  margin: 0 auto;
  background: #FDFDFD;
  padding: 1.5rem 1rem;
  font-size: 1.4rem;
  line-height: 1.7rem;
  text-align: center;
  font-weight: 500;
  color: #005DAB;
  border: 1px solid #005DAB;
  transition: all 0.3s;
}
.vacancy-resume__form-btn:hover {
  color: #FDFDFD;
  background: #005DAB;
}
.vacancy__link {
  text-decoration: underline;
  transition: all 0.3s ease;
}
.vacancy__link:hover {
  color: #005DAB;
  text-decoration: underline;
}

span.required {
  display: inline;
  color: rgba(255, 0, 0, 0.6);
}

input.required {
  width: 100%;
}

.required__wrapper {
  display: block;
  width: 100%;
  position: relative;
}
.required__wrapper::before {
  position: absolute;
  content: "*";
  color: rgba(255, 0, 0, 0.6);
  left: 0.2rem;
  top: 0;
}

.projects__gallery {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}
.projects__gallery-item {
  transition: all 0.3s ease;
}
.projects__gallery-item:hover {
  transform: scale(1.5);
}
.projects__gallery-img {
  width: 100%;
  box-shadow: 0px 4px 7px 0px rgba(0, 0, 0, 0.2);
  border-radius: 1rem;
}
.projects__img {
  display: block;
  margin: 0 auto;
  width: 70%;
  box-shadow: 0px 4px 7px 0px rgba(0, 0, 0, 0.2);
  border-radius: 1rem;
}

.news__item {
  display: none;
}
.news__item--active {
  display: block;
}
.news__subtitle {
  font-weight: 500;
  color: #005DAB;
  transition: all 0.3s ease;
}
.news__card-link {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  padding: 1rem 0.5rem;
  transition: all 0.3s;
  border-bottom: 1px solid rgba(0, 93, 171, 0.2);
}
.news__card-link:hover {
  color: #FDFDFD;
  background: #005DAB;
}
.news__card-link:hover .news__subtitle {
  color: #FDFDFD;
}
.news__link {
  text-decoration: none;
  transition: all 0.3s ease;
  padding: 0;
}
.news__link-item {
  margin: 0 auto;
}
.news__link:hover {
  padding-bottom: 0.5rem;
  border-bottom: 1px solid #005dab;
}
.news__list {
  padding-left: 0 !important;
}
.news__list-item {
  display: inline;
  list-style-type: none !important;
}
.news__list-item + .news__list-item::before {
  content: " ";
  padding-left: 0.5rem;
}
.news__list-item + .news__list-item + .news__list-item::before {
  content: "/ ";
  padding-left: 0.5rem;
}
.news__list-link {
  text-decoration: underline;
  transition: color 0.3s;
}
.news__list-link:hover {
  color: #005DAB;
}
.news__block {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.news__block-link {
  font-size: 1.4rem;
}

.contacts-feedback__link {
  text-decoration: underline;
  transition: all 0.3s ease;
}
.contacts-feedback__link:hover {
  color: #005DAB;
  text-decoration: underline;
}
.contacts-feedback__form {
  display: flex;
  flex-direction: column;
}
.contacts-feedback__form-item {
  padding: 1rem 1.5rem 1rem;
  margin-bottom: 1rem;
  font-size: 1.4rem;
  border: 1px solid #FDFDFD;
  border-bottom: 1px solid #005DAB;
  text-align: center;
  transition: border 0.5s;
}
.contacts-feedback__form-item::placeholder {
  font-size: 1.4rem;
  text-align: center;
}
.contacts-feedback__form-item:focus {
  border: 1px solid #005DAB;
  color: #252525;
}
.contacts-feedback__form-item:focus::placeholder {
  color: #FDFDFD;
}
.contacts-feedback__form-info {
  display: flex;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
.contacts-feedback__form-info input {
  display: block;
  width: 100%;
  border: 1px solid #FDFDFD;
  border-bottom: 1px solid #005DAB;
  padding: 0.5rem;
  font-size: 1.4rem;
  margin-bottom: 0.5rem;
  transition: border 0.5s;
}
.contacts-feedback__form-info input:focus {
  border: 1px solid #005DAB;
  color: #252525;
}
.contacts-feedback__form-more {
  text-align: center;
  margin: 0 auto;
  width: 100%;
}
.contacts-feedback__form-more p {
  margin-bottom: 1rem;
}
.contacts-feedback__form-more textarea {
  border: 1px solid #FDFDFD;
  border-bottom: 1px solid #005DAB;
  border-top: 1px solid #005DAB;
  width: 50%;
  height: 10rem;
  padding: 0.5rem;
  resize: none;
  transition: border 0.5s;
}
.contacts-feedback__form-more textarea::placeholder {
  text-align: center;
}
.contacts-feedback__form-more textarea:focus {
  border: 1px solid #005DAB;
  color: #252525;
}
.contacts-feedback__form-btn {
  display: block;
  margin: 0 auto;
  background: #FDFDFD;
  padding: 1.5rem 1rem;
  font-size: 1.4rem;
  line-height: 1.7rem;
  text-align: center;
  font-weight: 500;
  color: #005DAB;
  border: 1px solid #005DAB;
  transition: all 0.3s;
}
.contacts-feedback__form-btn:hover {
  color: #FDFDFD;
  background: #005DAB;
}

.map {
  width: 100%;
  height: 50rem;
  margin: 0;
  box-shadow: 0px 4px 7px 0px rgba(0, 0, 0, 0.2);
}
.map:hover {
  transform: none;
}
.map__link {
  text-align: center;
}

.translate {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 1rem;
  gap: 5rem;
  height: 100vh;
}
.translate__text {
  font-size: 3rem;
  color: #005DAB;
  margin-bottom: 2rem;
}
.hero__menu-mobile {
  display: none;
}

.hero__menu-list {
  display: none;
}

.hero__menu-btn {
  display: none;
  position: relative;
  width: 3.5rem;
  height: 2.4rem;
}

.image__gallery, .manufacture__gallery, .projects__gallery, .product__gallery {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}
.image__gallery-part4, .manufacture__gallery-part4, .projects__gallery-part4, .product__gallery-part4 {
  grid-template-columns: repeat(3, 1fr);
}
.image__gallery-part4 img, .manufacture__gallery-part4 img, .projects__gallery-part4 img, .product__gallery-part4 img {
  width: 100%;
  transition: all 0.3s ease;
}
.image__gallery-item, .manufacture__gallery-item, .projects__gallery-item, .product__gallery-item {
  transition: all 0.3s ease;
}
.image__gallery-item:hover, .manufacture__gallery-item:hover, .projects__gallery-item:hover, .product__gallery-item:hover {
  transform: scale(1.2);
}
.image__gallery-img, .manufacture__gallery-img, .projects__gallery-img, .product__gallery-img {
  width: 100%;
  box-shadow: 0px 4px 7px 0px rgba(0, 0, 0, 0.2);
  border-radius: 1rem;
}
.image__img, .manufacture__img, .projects__img, .product__img {
  display: block;
  margin: 0 auto;
  width: 70%;
  box-shadow: 0px 4px 7px 0px rgba(0, 0, 0, 0.2);
  border-radius: 1rem;
}

.table {
  box-shadow: 0px 4px 7px 0px rgba(0, 0, 0, 0.2);
}
.table-title {
  padding: 0.5rem;
  color: #FDFDFD;
  background: #005DAB;
}
.table-td {
  padding: 0.5rem;
  background: rgba(0, 93, 171, 0.1);
}

.footer {
  margin: 3rem 0;
}
.footer__inner {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.footer__menu {
  margin-bottom: 1rem;
}
.footer__list {
  display: flex;
  gap: 2rem;
}
.footer__list-link {
  font-size: 1.3rem;
  color: #005DAB;
  transition: all 0.3s;
}
.footer__list-link:hover {
  padding-bottom: 0.5rem;
  border-bottom: 1px solid #005DAB;
}
.footer__info {
  color: gray;
  font-size: 1.3rem;
  margin-top: 0.5rem;
}
.footer__info a {
  color: inherit;
  transition: color 0.3s;
}
.footer__info a:hover {
  color: #005DAB;
}

@media (max-width: 992px) {
  .page__inner {
    display: block;
  }

  .page__link {
    padding: 1rem 0;
    margin-bottom: 1rem;
  }
  .page__link:hover {
    text-decoration: underline;
  }

  .wrapper ul,
.wrapper ol {
    padding-left: 3rem;
  }
  .wrapper ul li {
    padding: 1rem 0;
  }
  .wrapper ol li {
    padding: 1rem 0;
  }

  .header__main {
    flex-direction: column;
    text-align: center;
    align-items: center;
  }

  .main__top {
    gap: 1rem;
  }

  .menu__sublist-item {
    display: none;
  }

  .aside {
    display: none;
  }
}
@media (max-width: 768px) {
  .mobile__logo-link {
    display: block;
    text-align: center;
    margin: 1rem 0;
  }
  .mobile__title {
    display: block;
    font-size: 2rem;
  }

  iframe {
    width: 100%;
  }
  iframe:hover {
    transform: none;
  }

  .page__title {
    font-size: 2.4rem;
    text-align: center;
    margin-bottom: 1rem;
  }
  .page__text {
    font-size: 1.8rem;
    line-height: 2.4rem;
  }

  .breadcrumb {
    display: none;
  }

  .image__gallery, .manufacture__gallery, .projects__gallery, .product__gallery {
    grid-template-columns: none;
  }
  .image__gallery-item:hover, .manufacture__gallery-item:hover, .projects__gallery-item:hover, .product__gallery-item:hover {
    transform: none;
  }
  .image__img, .manufacture__img, .projects__img, .product__img {
    display: block;
    margin: 0 auto;
    width: 100%;
    box-shadow: 0px 4px 7px 0px rgba(0, 0, 0, 0.2);
    border-radius: 1rem;
  }

  .up {
    display: none;
  }

  .header {
    display: none;
  }
  .header .header__lang {
    display: none;
  }

  .hero__inner {
    padding: 1rem 0 10rem;
  }
  .hero__lang {
    display: none;
  }
  .hero__menu-mobile {
    display: block;
  }
  .hero__photo {
    width: 100%;
    min-height: 39rem;
    background: url(/../assets/img/photo.jpg) no-repeat;
    background-position: 60% 40%;
    background-size: 250%;
  }

  .hero__menu {
    order: 1;
  }
  .hero__menu-btn {
    display: block;
    background: transparent;
    position: fixed;
    top: 2rem;
    right: 1rem;
    z-index: 2;
  }
  .hero__menu-btn--active {
    opacity: 0.3;
  }
  .hero__menu-btn-line {
    display: block;
    width: 100%;
    height: 0.2rem;
    background: black;
    position: absolute;
    left: 0;
    transition: all 0.3s linear;
  }
  .hero__menu-btn-line--first {
    top: 0;
  }
  .hero__menu-btn-line--second, .hero__menu-btn-line--third {
    top: 50%;
    transform: translateY(-50%);
  }
  .hero__menu-btn-line--fourth {
    bottom: 0;
    color: black;
  }
  .hero__menu-btn--active .hero__menu-btn-line--first, .hero__menu-btn--active .hero__menu-btn-line--fourth {
    opacity: 0;
  }
  .hero__menu-btn--active .hero__menu-btn-line--second {
    transform: translateY(-50%) rotate(45deg);
  }
  .hero__menu-btn--active .hero__menu-btn-line--third {
    transform: translateY(-50%) rotate(-45deg);
  }
  .hero__menu-list {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    height: 100vh;
    position: fixed;
    text-align: center;
    opacity: 0.95;
    top: 0;
    left: 0;
    right: 0;
    padding: 2rem 1rem;
    background-color: #ffffff;
    transform: translatex(80rem);
    transition: all 0.5s;
  }
  .hero__menu-list.hero__menu-list--active {
    z-index: 1;
    transform: translateY(0);
  }
  .hero__menu-list-link {
    display: block;
    font-size: 1.8rem;
    line-height: 2.8rem;
    padding: 2rem 0;
  }
  .hero__menu .header__logo-link {
    display: block;
    margin: 0 auto;
  }

  .certificates {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  .certificates__img-box:hover {
    transform: none;
  }

  .main__top {
    text-align: center;
  }
  .main__list {
    padding: 0 0.5rem;
  }
  .main__list-title {
    font-size: 1.6rem;
  }
  .main__list-link {
    padding: 0.5rem 0;
    font-size: 1.8rem;
  }

  .news__link:hover {
    padding: 0;
    border: none;
  }
}
@media (max-width: 576px) {
  .mobile__title {
    font-size: 1.8rem;
  }
  .mobile__title span {
    display: block;
    font-size: 1.4rem;
  }

  .main__preview {
    text-align: center;
  }
}
@media (max-width: 480px) {
  .main__list {
    margin-bottom: 2rem;
  }
  .main__list-title {
    margin-bottom: 1rem;
  }
  .main__top {
    display: block;
    text-align: left;
  }

  .hero__top {
    display: block;
  }
  .hero__title {
    margin-bottom: 1rem;
  }
  .hero__text {
    text-align: left;
  }
  .hero__photo {
    background-size: 400%;
  }
}
@media (max-width: 320px) {
  .hero__menu-mobile {
    order: 2;
  }
}
body.lb-disable-scrolling {
  overflow: hidden;
}

.lightboxOverlay {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9999;
  background-color: black;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=80);
  opacity: 0.8;
  display: none;
}

.lightbox {
  position: absolute;
  left: 0;
  width: 100%;
  z-index: 10000;
  text-align: center;
  line-height: 0;
  font-weight: normal;
  outline: none;
}

.lightbox .lb-image {
  display: block;
  height: auto;
  max-width: inherit;
  max-height: none;
  border-radius: 3px;
  /* Image border */
  border: 4px solid white;
}

.lightbox a img {
  border: none;
}

.lb-outerContainer {
  position: relative;
  *zoom: 1;
  width: 250px;
  height: 250px;
  margin: 0 auto;
  border-radius: 4px;
  /* Background color behind image.
     This is visible during transitions. */
  background-color: white;
}

.lb-outerContainer:after {
  content: "";
  display: table;
  clear: both;
}

.lb-loader {
  position: absolute;
  top: 43%;
  left: 0;
  height: 25%;
  width: 100%;
  text-align: center;
  line-height: 0;
}

.lb-cancel {
  display: block;
  width: 32px;
  height: 32px;
  margin: 0 auto;
  background: url(../img/lightbox-ico/loading.gif) no-repeat;
}

.lb-nav {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 10;
}

.lb-container > .nav {
  left: 0;
}

.lb-nav a {
  outline: none;
  background-image: url("data:image/gif;base64,R0lGODlhAQABAPAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw==");
}

.lb-prev, .lb-next {
  height: 100%;
  cursor: pointer;
  display: block;
}

.lb-nav a.lb-prev {
  width: 34%;
  left: 0;
  float: left;
  background: url(../img/lightbox-ico/prev.png) left 48% no-repeat;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0);
  opacity: 0;
  -webkit-transition: opacity 0.6s;
  -moz-transition: opacity 0.6s;
  -o-transition: opacity 0.6s;
  transition: opacity 0.6s;
}

.lb-nav a.lb-prev:hover {
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
  opacity: 1;
}

.lb-nav a.lb-next {
  width: 64%;
  right: 0;
  float: right;
  background: url(../img/lightbox-ico/next.png) right 48% no-repeat;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0);
  opacity: 0;
  -webkit-transition: opacity 0.6s;
  -moz-transition: opacity 0.6s;
  -o-transition: opacity 0.6s;
  transition: opacity 0.6s;
}

.lb-nav a.lb-next:hover {
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
  opacity: 1;
}

.lb-dataContainer {
  margin: 0 auto;
  padding-top: 5px;
  *zoom: 1;
  width: 100%;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.lb-dataContainer:after {
  content: "";
  display: table;
  clear: both;
}

.lb-data {
  padding: 0 4px;
  color: #ccc;
}

.lb-data .lb-details {
  width: 85%;
  float: left;
  text-align: left;
  line-height: 1.1em;
}

.lb-data .lb-caption {
  font-size: 13px;
  font-weight: bold;
  line-height: 1em;
}

.lb-data .lb-caption a {
  color: #4ae;
}

.lb-data .lb-number {
  display: block;
  clear: left;
  padding-bottom: 1em;
  font-size: 12px;
  color: #999999;
}

.lb-data .lb-close {
  display: block;
  float: right;
  width: 30px;
  height: 30px;
  background: url(../img/lightbox-ico/close.png) top right no-repeat;
  text-align: right;
  outline: none;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=70);
  opacity: 0.7;
  -webkit-transition: opacity 0.2s;
  -moz-transition: opacity 0.2s;
  -o-transition: opacity 0.2s;
  transition: opacity 0.2s;
}

.lb-data .lb-close:hover {
  cursor: pointer;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
  opacity: 1;
}