.image,.manufacture,.projects,.product {
  &__gallery {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;

    &-part4{
      grid-template-columns: repeat(3, 1fr);
      img{
        width: 100%;
        transition: all .3s ease;
        // object-fit: cover;

        &:hover{
          // transform: scale(1.2);
        }
      }
    }

    &-item {
      transition: all .3s ease;

      &:hover {
        transform: scale(1.2);
      }
    }

    &-img {
      width: 100%;
      box-shadow: 0px 4px 7px 0px rgba(0, 0, 0, .2);
      border-radius: 1rem;
    }
  }

  &__img {
    display: block;
    margin: 0 auto;
    width: 70%;
    box-shadow: 0px 4px 7px 0px rgba(0, 0, 0, .2);
    border-radius: 1rem;
  }
}