.vacancy{
  &-resume {

    &__link {
      text-decoration: underline;
      transition: all .3s ease;
  
      &:hover {
        color: $color2;
        text-decoration: underline;
      }
    }
  
    &__form {
      display: flex;
      flex-direction: column;
  
      &-warning {
        font-size: 1.2rem;
      }
  
      &-item {
        padding: 1rem 1.5rem 1rem;
        margin-bottom: 1rem;
        font-size: 1.4rem;
        border: 1px solid $color4;
        border-bottom: 1px solid $color2;
        text-align: center;
        transition: border .5s;
  
        &::placeholder {
          font-size: 1.4rem;
          text-align: center;
        }
  
        &:focus {
          border: 1px solid $color2;
          color: $color1;
  
          &::placeholder {
            color: $color4;
          }
        }
      }
  
      &-info {
        display: flex;
        margin-top: 1rem;
        margin-bottom: 1rem;
  
        input {
          display: block;
          width: 100%;
          border: 1px solid $color4;
          border-bottom: 1px solid $color2;
          padding: .8rem;
          font-size: 1.4rem;
          margin-bottom: 0.5rem;
  
          transition: border .5s;
  
          &:focus {
            border: 1px solid $color2;
            color: $color1;
          }
        }
      }
  
      &-block {
        display: flex;
        flex-direction: column;
        gap: 0.5rem;
        padding: 0.5rem;
  
        p {
          font-size: 1.4rem;
          line-height: 1;
          margin-bottom: auto;
        }
      }
  
      &-more {
        text-align: center;
        margin: 0 auto;
        width: 100%;
  
        p {
          margin-bottom: 1rem;
        }
  
        textarea {
          border: 1px solid $color4;
          border-bottom: 1px solid $color2;
          border-top: 1px solid $color2;
          width: 50%;
          height: 5rem;
          padding: 0.5rem;
          // overflow: hidden;
          resize: none;
          transition: border .5s;
  
          &::placeholder {
            text-align: center;
          }
  
          &:focus {
            border: 1px solid $color2;
  
            color: $color1;
          }
        }
      }
  
      &-btn {
        display: block;
        margin: 0 auto;
        background: $color4;
        padding: 1.5rem 1rem;
        font-size: 1.4rem;
        line-height: 1.7rem;
        text-align: center;
        font-weight: 500;
        color: $color2;
        border: 1px solid $color2;
        transition: all .3s;
  
        &:hover {
          color: $color4;
          background: $color2;
        }
      }
    }
  }
  &__link{
    text-decoration: underline;
    transition: all .3s ease;
    
    &:hover {
      color: $color2;
      text-decoration: underline;
    }
  }
}
span.required {
  display: inline;
  color: rgba(255, 0, 0, .6);
}

input.required {
  width: 100%;
}

.required__wrapper{
  display: block;
  width: 100%;
  position: relative;
  &::before {
    position: absolute;
    content: "*";
    color: rgba(255, 0, 0, .6);
    left: 0.2rem;
    top: 0;
  }
}